import { Type, Struct } from 'rcfm'
import getProp from '../base/get-prop'
import joinClass from '../base/join-class'



function getProps(props = {}) {
  let type = getProp(props, 'type', { default: 'button' })
  let variant = getProp(props, 'variant', { default: '' })
  let color = getProp(props, 'color', { default: 'primary' })
  let size = getProp(props, 'size', { default: '' })
  let fluid = 'fluid' in props && props.fluid !== false
  let circle = 'circle' in props && props.circle !== false
  let active = 'active' in props && props.active !== false
  let disabled = 'disabled' in props && props.disabled !== false
  let onClick = getProp(props, 'onClick', { default: null })
  let icon = getIcon(props)
  let className = joinClass(props.className, [
    variant ? `rcfm-btn-${variant}-${color}` : `rcfm-btn-${color}`,
    size ? `rcfm-btn-${size}` : '', fluid ? 'rcfm-btn-fluid' : '',
    circle ? `rcfm-btn-circle` : '', active ? 'rcfm-btn-active' : '',
    icon.position ? `rcfm-btn-ico-${icon.position}` : '',
  ])
  if (disabled) { onClick = null }
  return {
    iconProps: icon,
    buttonProps: {
      ...Struct.object.exclude(props, [
        'type', 'variant', 'color', 'size', 'fluid', 'circle', 'active',
        'disabled', 'onClick', 'icon', 'topIcon', 'leftIcon', 'rightIcon',
      ]), type, className, disabled, onClick,
    }
  }
}

function getIcon(props = {}) {
  if ('icon' in props) {
    return {
      position: 'middle',
      name: Type.is.string(props.icon) ? props.icon : '',
    }
  }
  if ('topIcon' in props) {
    return {
      position: 'top',
      name: Type.is.string(props.topIcon) ? props.topIcon : '',
    }
  }
  if ('leftIcon' in props) {
    return {
      position: 'left',
      name: Type.is.string(props.leftIcon) ? props.leftIcon : '',
    }
  }
  if ('rightIcon' in props) {
    return {
      position: 'right',
      name: Type.is.string(props.rightIcon) ? props.rightIcon : '',
    }
  }
  return {}
}



const Group = (() => {

  function getProps(props = {}) {
    let direction = 'vertical' in props ? 'vertical' : 'horizontal'
    let fluid = 'fluid' in props && props.fluid !== false
    let className = joinClass(props.className, [
      direction === 'horizontal' ? 'rcfm-btngroup-x' : 'rcfm-btngroup-y',
      fluid ? 'rcfm-btngroup-fluid' : '',
    ])
    return {
      ...Struct.object.exclude(props, ['vertical', 'fluid']), className,
    }
  }

  return { getProps }

})()



const Helper = {
  Group,
  getProps,
}



export default Helper
