import { Struct } from 'rcfm'
import getProp from '../base/get-prop'
import joinClass from '../base/join-class'



function getProps(props = {}) {
  let type = getProp(props, 'type', { default: 'info' })
  let hint = getProp(props, 'hint', { default: '' })
  let on = !('on' in props) || props.on !== false
  let className = joinClass(props.className, [
    `rcfm-highlight-${type}`, !on ? 'rcfm-highlight-off' : '',
  ])
  return {
    on, hintProps: { type, hint, className: 'rcfm-highlight-hint' },
    wrapperProps: {
      ...Struct.object.exclude(props, ['type', 'hint', 'on', 'className']),
      className,
    },
  }
}



const Helper = { getProps }



export default Helper
