import getProp from '../../../base/get-prop'
import i18n from '../locales'



const Helper = {

  getProps(props = {}) {
    let value = getProp(props, 'value', { default: undefined })
    let onChange = getProp(props, 'onChange')
    let options = [{
      value: true,
      label: i18n.t('section.dataType.editor.boolean.option.true'),
    }, {
      value: false,
      label: i18n.t('section.dataType.editor.boolean.option.false'),
    }]
    return { ...props, value, options, onChange }
  },

}



export default Helper
