import getProp from '../../../base/get-prop'



const Helper = {

  required(reactive, member) {
    let schema = reactive.schema.for({ id: member.id, prop: 'value' })
    let validation = reactive.get({ id: member.id, prop: 'validation' })
    let validations = (schema.validation || []).concat(validation || [])
    let presenceValidation = validations.find((v) => 'presence' in v)
    return presenceValidation ? true : false
  },

  readonly(reactive, member){
    let id = member.id
    if (!reactive.get({ id, prop: 'editable' })) { return true }
    if (!reactive.get({ id, prop: 'visible' })) { return true }
    let state = reactive.get({ id, prop: 'calculation' }, { include: 'raw' })
    if (state.raw.value) { return true }
    return false
  },

}



export default Helper
