import { Type } from 'rcfm'
import getProp from '../../../../../base/get-prop'
import joinClass from '../../../../../base/join-class'



function mutate(reactive, member, options = [], mutation = {}) {
  options = options.slice()
  let { index, action, label, value } = mutation
  if (action === 'delete') {
    options.splice(index, 1)
  } else if (action === 'append') {
    options.splice(index + 1, 0, { label: '', value: '' })
  } else if (action === 'update') {
    if ('label' in mutation) { options[index].label = label }
    if ('value' in mutation) { options[index].value = value }
  }
  return options
}



const Helper = {

  getProps(props = {}) {
    let reactive = getProp(props, 'reactive', { required: true })
    let member = getProp(props, 'member', { required: true })
    let onChange = getProp(props, 'onChange', { required: true })
    let exception = getProp(props, 'exception', { default: null })
    let value = getProp(props, 'value', {
      default: [], validate(val) { Type.mustbe.array(val) },
    })
    if (!value.length) { value = [{}] }
    return { reactive, member, value, exception, onChange(mu) {
      onChange(value = mutate(reactive, member, value, mu))
    } }
  },

}



export default Helper
