import { Type } from 'rcfm'
import getProp from '../../../../base/get-prop'
import joinClass from '../../../../base/join-class'
import ConditionHelper from './condition'
import OptionsHelper from './enhanced/options'
import ValidationHelper from './enhanced/validation'
import i18n from '../../locales'



const Helper = {

  Condition: ConditionHelper,

  Options: OptionsHelper,
  Validation: ValidationHelper,

  getProps(props = {}) {
    let reactive = getProp(props, 'reactive', { required: true })
    let member = getProp(props, 'member', {
      required: true, validate(val) { Type.mustbe.nonblankString(val.prop) },
    })
    return { reactive, member }
  },

  getTabsProps(props = {}) {
    let { reactive, member } = Helper.getProps(props)
    let { id, prop } = member
    const Condition = reactive.schema.constructor.Condition
    let value = reactive.get(member, { include: 'raw' }).raw.value
    let { schemable, label } = reactive.schema.for(member)
    let isCondition = Condition.isCondition(value)
    if (!schemable) { isCondition = false }
    let activeIndex = isCondition ? 1 : 0
    let labels = !schemable ? [] : [
      i18n.t('section.form.member.propEditor.tabs.labels.always'),
      i18n.t('section.form.member.propEditor.tabs.labels.conditional'),
    ]
    let defaultResult = () => reactive.schema.for(member).default
    let onChange = (value) => props.onAction({
      action: 'update', reactive, id, prop, value,
    })
    let onSwitch = (index) => onChange(
      !schemable || !index ? defaultResult() : [
        { when: [{}], then: defaultResult() },
        { else: defaultResult() },
      ]
    )
    return {
      title: label, labels, activeIndex,
      onChange, onSwitch, defaultResult,
    }
  },

  getPropType(reactive, member) {
    let { validation } = reactive.schema.for(member)
    if (!validation) { return 'Object' }
    return validation.find((v) => 'dataType' in v)?.dataType || 'Object'
  },

}



export default Helper
