import { Type } from 'rcfm'
import getProp from '../../../base/get-prop'
import joinClass from '../../../base/join-class'



const Helper = {

  Actions: {

    getProps(props = {}) {
      let actions = getProp(props, 'action', {
        default: ['delete', 'increase', 'decrease', 'edit'],
        validate(val) { Type.mustbe.array(val) },
      })
      let onAction = getProp(props, 'onAction', {
        required: actions && actions.length,
        valudate(val) { Type.mustbe.function(val) },
      })
      return { actions, onAction }
    },

  },


}



export default Helper
