import { Type, Struct } from 'rcfm'
import getProp from '../base/get-prop'
import joinClass from '../base/join-class'



function collectProps(props = {}, conf = {}) {
  let [rootClass] = [conf.rootClass || '']
  let [only, except] = [conf.builtinOnly || [], conf.builtinExcept || []]
  let acceptCustom = (() => {
    let acceptSpecificCustom = Type.is.array(conf.acceptCustom)
    if (acceptSpecificCustom) { return (p) => conf.acceptCustom.includes(p) }
    return () => conf.acceptCustom ? true : false
  })()
  let type = getProp(props, 'type', { default: '' })
  let value = getProp(props, 'value', { default: undefined })
  let name = getProp(props, 'name', { default: '' })
  let placeholder = getProp(props, 'placeholder', { default: '' })
  let invalid = 'invalid' in props && props.invalid !== false
  let disabled = 'disabled' in props && props.disabled !== false
  let readonly = 'readonly' in props && props.readonly !== false
  let multiple = 'multiple' in props && props.multiple !== false
  let multiline = 'multiline' in props && props.multiline !== false
  let includeblank = 'includeblank' in props && props.includeblank !== false
  let size = getProp(props, 'size', { default: '' })
  let debounce = getProp(props, 'debounce', { default: null })
  let options = getProp(props, 'options', {
    default: [],
    validate(opts) {
      Type.mustbe.array(opts)
      opts.forEach((o) => {
        Type.mustbe.object(o, 'Option')
        Type.mustbe.string(o.label, 'Option label')
      })
    },
  })
  let onChange = (() => {
    if (readonly || disabled) { return null }
    return getProp(props, 'onChange', { default: null })
  })()
  let className = joinClass([
    acceptCustom('className') ? props.className : '', 'rcfm-fmtag', rootClass,
    size ? `rcfm-fm-${size}` : '', invalid ? 'rcfm-fm-invalid' : '',
  ])
  let builtinProps = {
    name, type, value, placeholder, invalid, disabled, readonly,
    multiple, multiline, includeblank, size, debounce, options,
    onChange, className,
  }
  let customProps = Struct.object.exclude(props, Object.keys(builtinProps))
  only.length && (builtinProps = Struct.object.collect(builtinProps, only))
  except.length && (builtinProps = Struct.object.exclude(builtinProps, except))
  let acceptedCustom = Object.keys(customProps).filter((p) => acceptCustom(p))
  customProps = Struct.object.collect(customProps, acceptedCustom)
  return { ...customProps, ...builtinProps }
}



const TextInput = (() => {

  function getProps(props = {}) {
    let { multiline } = collectProps(props, { builtinOnly: ['multiline'] })
    let wrapperProps = collectProps(props, {
      acceptCustom: ['className'],
      builtinOnly: ['className'],
      rootClass: multiline ? 'rcfm-fmtextarea' : 'rcfm-fminput',
    })
    let inputProps = collectProps(props, {
      acceptCustom: true,
      builtinOnly: [
        'type', 'name', 'placeholder',
        'disabled', 'readonly', 'debounce', 'onChange',
      ],
    })
    inputProps.className = 'rcfm-fmtag'
    inputProps.value = getProp(props, 'value', { default: '' })
    !multiline && (inputProps.type = getProp(props, 'type', { default: '' }))
    return { multiline, inputProps, wrapperProps }
  }

  return { getProps }

})()



const NumberInput = (() => {

  function getProps(props = {}) {
    let wrapperProps = collectProps(props, {
      acceptCustom: ['className'],
      builtinOnly: ['className'],
      rootClass: 'rcfm-fminput',
    })
    let inputProps = collectProps(props, {
      acceptCustom: true,
      builtinOnly: [
        'type', 'name', 'placeholder',
        'disabled', 'readonly', 'debounce', 'onChange',
      ],
    })
    inputProps.type = 'number'
    inputProps.className = 'rcfm-fmtag'
    inputProps.value = getProp(props, 'value', {
      default: NaN, validate(val) { Type.mustbe.number(val) },
    })
    isNaN(inputProps.value) && (inputProps.value = '')
    return { inputProps, wrapperProps }
  }

  return { getProps }

})()



const Radios = (() => {

  function getProps(props = {}) {
    let value = getProp(props, 'value', { default: undefined })
    let { options } = collectProps(props, { builtinOnly: ['options'] })
    let wrapperProps = collectProps(props, {
      acceptCustom: true,
      builtinOnly: ['className'],
      rootClass: 'rcfm-fmradio',
    })
    let inputProps = collectProps(props, {
      acceptCustom: false,
      builtinOnly: [ 'name', 'disabled', 'readonly', 'onChange' ],
    })
    inputProps.type = 'radio'
    inputProps.className = 'rcfm-fmtag'
    return { inputProps, wrapperProps, value, options }
  }

  return { getProps }

})()



const Checkboxes = (() => {

  function getProps(props = {}) {
    let value = getProp(props, 'value', {
      default: [], validate(val) { Type.mustbe.array(val) },
    })
    let wrapperProps = collectProps(props, {
      acceptCustom: true,
      builtinOnly: ['className'],
      rootClass: 'rcfm-fmcheck',
    })
    let { inputProps, options } = Radios.getProps(props)
    inputProps.type = 'checkbox'
    return { wrapperProps, inputProps, value, options }
  }

  function getNewValue(event, value = [], toggledValue) {
    let newValue = value.slice()
    let isIncluded = newValue.includes(toggledValue)
    if (event.target.checked) {
      !isIncluded && newValue.push(toggledValue)
    } else {
      isIncluded && (newValue = newValue.filter((v) => v !== toggledValue))
    }
    return newValue
  }

  return { getProps, getNewValue }

})()



const SingleSelect = (() => {

  function getProps(props = {}) {
    let { includeblank, options } = collectProps(props)
    let value = getProp(props, 'value', { default: undefined })
    let wrapperProps = collectProps(props, {
      acceptCustom: ['className'],
      builtinOnly: ['className'],
      rootClass: 'rcfm-fmselect',
    })
    let inputProps = collectProps(props, {
      acceptCustom: true,
      builtinOnly: [
        'name', 'placeholder', 'disabled', 'readonly', 'onChange',
      ],
    })
    inputProps.className = 'rcfm-fmtag'
    return { inputProps, wrapperProps, includeblank, options, value }
  }

  function findLabel(value, options = []) {
    return options.find((o) => o.value === value)?.label || ''
  }

  return { getProps, findLabel }

})()



const MultipleSelect = (() => {

  function getProps(props = {}) {
    let value = getProp(props, 'value', {
      default: [], validate(val) { Type.mustbe.array(val) },
    })
    let { wrapperProps, inputProps, options } = SingleSelect.getProps(props)
    inputProps.multiple = true
    return { inputProps, wrapperProps, options, value }
  }

  return { getProps }

})()



const Field = (() => {

  function getWrapperProps(props = {}) {
    let size = getProp(props, 'size', { default: '' })
    let className = joinClass(props.className, [
      'rcfm-fmtag', 'rcfm-fmfield', size ? `rcfm-fmfield-${size}` : '',
    ])
    return {
      ...Struct.object.exclude(props, [
        'className', 'size', 'label', 'hint', 'error',
      ]), className,
    }
  }

  function getLabelProps(props = {}) {
    let label = getProp(props, 'label', { default: '' })
    if (!label) { return {} }
    let size = getProp(props, 'size', { default: '' })
    let required = 'required' in props && props.required !== false
    let className = joinClass([
      'rcfm-fmtag', 'rcfm-fmlabel', size ? `rcfm-fmlabel-${size}` : '',
      required ? 'rcfm-fmlabel-required' : '',
    ])
    return { className, label }
  }

  function getHintProps(props = {}) {
   let hint = getProp(props, 'hint', { default: '' })
    if (!hint) { return {} }
    let size = getProp(props, 'size', { default: '' })
    let className = joinClass([
      'rcfm-fmtag', 'rcfm-fmhint', size ? `rcfm-fmhint-${size}` : '',
    ])
    return { className, hint }
  }

  function getErrorProps(props = {}) {
    let errors = (() => {
      return (Type.is.array(props.errors) ? props.errors : [props.errors])
      .concat(props.error).flat(Infinity).filter((e) => e)
      .map((e) => e.message || e.name || e)
    })()
    if (!errors.length) { return {} }
    let size = getProp(props, 'size', { default: '' })
    let className = joinClass([
      'rcfm-fmtag', 'rcfm-fmerror', size ? `rcfm-fmerror-${size}` : '',
    ])
    return { className, error: errors }
  }

  function getProps(props = {}) {
    return {
      wrapperProps: getWrapperProps(props),
      labelProps: getLabelProps(props),
      hintProps: getHintProps(props),
      errorProps: getErrorProps(props),
    }
  }

  return { getProps }

})()



const Helper = {
  Field,
  TextInput,
  NumberInput,
  Radios,
  Checkboxes,
  SingleSelect,
  MultipleSelect,
}



export default Helper
