import { Form } from 'rcfm'
import i18n from '../locales'



const Helper = {

  FIELD_ICONS: {},

  getChildGroups() {
    return [{
      name: 'field',
      title: i18n.t('section.form.group.field.title'),
      types: (Form.group('field') || []).map((m) => {
        let i18nSegment = m.type[0].toLowerCase() + m.type.slice(1)
        return {
          type: m.type,
          icon: Helper.FIELD_ICONS[m.type] || 'plus',
          title: i18n.t(`section.form.field.${i18nSegment}.title`),
        }
      }),
    }]
  },

}



export default Helper
