import { Type } from 'rcfm'
import getProp from '../../../base/get-prop'
import PropEditor from './prop-editor'
import Previewer from './previewer'



const Helper = {

  PropEditor,
  Previewer,

  getProps(props) {
    let reactive = getProp(props, 'reactive', { required: true })
    let member = getProp(props, 'member', { required: true })
    let onAction = getProp(props, 'onAction', {
      default: null, validate(val) { Type.mustbe.function(val) },
    })
    return { reactive, member, onAction }
  },

  joinErrors(errors = {}) {
    if (!errors) { return '' }
    errors = Object.values(errors).flat(Infinity)
    if (!errors.length) { return '' }
    return errors.map((e) => e.message).join(' ')
  }

}



export default Helper
